* {
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

html, main {
  width: 100vw;
  height: 100vh;
  font-family: Poppins, sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  color: #000;
}

p {
  color: #666;
  margin-top: 10px;
  font-weight: 200;
  line-height: 28px;
}

.big-text {
  font-size: 60px;
  font-weight: 600;
}

.medium-text {
  font-size: 40px;
  font-weight: 500;
}

.normal-text {
  font-size: 20px;
  font-weight: 300;
}

.small-text {
  font-size: 16px;
  font-weight: 200;
}

a {
  text-decoration: none;
}

@media (width <= 768px) {
  .big-text {
    font-size: 35px;
  }

  .medium-text {
    font-size: 28px;
  }

  .normal-text {
    font-size: 16px;
  }

  .small-text {
    font-size: 14px;
  }
}

#first-page {
  margin-left: 2rem;
  margin-right: 2rem;
}

.menucontent {
  color: #000;
  background: #000;
  border-radius: 10px;
  width: 28px;
  height: 4px;
  margin: 5px 0;
  transition: all .2s ease-in-out;
}

#menu-button {
  z-index: 999999;
  background: none;
  border: none;
  position: fixed;
  top: 30px;
  right: 30px;
}

#menu {
  z-index: 99999;
  background: #fff;
  width: 100vw;
  height: 100vh;
  transition: all .4s cubic-bezier(.215, .61, .355, 1);
  position: fixed;
  left: 100vw;
}

#menu.is-active {
  top: 0;
  left: 0;
}

.is-active .bar2 {
  opacity: 0;
}

.is-active .bar1 {
  transform: rotate(-45deg)translate(-13px, 11px);
}

.is-active .bar3 {
  transform: rotate(45deg)translate(-1px);
}

#menu ul {
  margin: 5px;
  padding-top: 80px;
}

#menu ul li {
  text-align: center;
  padding-top: 20px;
  list-style-type: none;
}

#menu ul li a {
  color: #000;
  font-weight: 200;
  text-decoration: none;
}

#menu ul li a:hover {
  color: #353535;
  font-size: 22px;
  font-weight: 600;
  transition: all .5s ease-out;
}

#title {
  padding-top: 30px;
}

#subtitle {
  padding-top: 7px;
  font-weight: 200;
}

.header {
  background: none;
  padding-bottom: 30px;
}

.hero {
  background-image: url("bg-img.642e564b.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
}

.hero__content {
  width: 100%;
  margin: 0 auto;
}

@media (width <= 768px) {
  .hero__content {
    margin-left: 15px;
  }
}

.about {
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
}

.about__img {
  width: 60%;
  height: 100%;
}

.about__img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.about__content {
  width: 40%;
  margin-right: 2rem;
  padding: 50px;
}

.Beat {
  align-items: center;
  width: 100%;
  height: 100vh;
  margin-top: 100px;
  display: flex;
}

.beat-copertina {
  max-width: 60%;
  height: auto;
  margin: auto;
  display: block;
}

.beat_img {
  align-items: center;
  width: 100%;
  height: 100%;
  margin-bottom: 100px;
}

.beat_img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.Beat_content {
  align-items: center;
  margin: 3rem;
}

.beat_homepage {
  align-items: center;
  margin-top: 100px;
}

.beat_homepage img {
  width: 60%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.mockup_beat {
  width: 50%;
}

@media (width <= 768px) {
  .about {
    flex-wrap: wrap;
    height: auto;
    margin-top: 100px;
  }

  .about__img, .about__content {
    width: 100%;
  }
}

.grid {
  max-width: 1350px;
  margin: 150px auto 0;
  padding: 0 30px;
  display: flex;
}

.grid .col {
  width: 100%;
}

@media (width <= 768px) {
  .grid {
    flex-wrap: wrap;
  }

  .grid .col {
    width: 100%;
    margin-left: 20px;
  }
}

.footer {
  padding: 150px 0;
}

@media (width <= 1920px) {
  #subtitle {
    padding-top: 0;
    font-size: 18px;
  }

  #title {
    font-size: 28px;
    font-weight: 0;
  }

  #menu {
    font-size: 18px;
  }

  p {
    font-weight: 200;
  }
}

@media (width <= 980px) {
  #subtitle {
    font-size: 18px;
  }

  #title, #menu {
    font-size: 20px;
  }
}

@media (width <= 767px) {
  #subtitle {
    font-size: 16px;
  }

  #title, #menu {
    font-size: 18px;
  }
}

@media (width <= 480px) {
  #subtitle {
    padding-left: 15px;
    font-size: 14px;
  }

  #title {
    padding-left: 15px;
    font-size: 16px;
  }

  #menu {
    font-size: 16px;
  }

  #menu-button {
    padding-right: 0;
  }

  .menucontent {
    width: 20px;
    height: 3px;
    margin: 3px 0;
  }

  .is-active .bar2 {
    opacity: 0;
  }

  .is-active .bar1 {
    transform: rotate(-45deg)translate(-10px, 6px);
  }

  .is-active .bar3 {
    transform: rotate(45deg)translate(-2px, 1px);
  }
}

